import Box from "../UI/Box";

import style from "./Help.module.css";
import whiteMusicalNote from "../../images/white_musical_note.png"

const Help = () => {
  return (
    <div className={style.help}>
      <Box title="Help" dropdown={false} icon={whiteMusicalNote}>
        <div className={style.notes}>
          <h3>Musical Notes</h3>
          <p>
            Genshin lyre has a range from C3 (as "do") to B5 without sharp or
            flat notes.
          </p>
        </div>
        <div className={style.notes}>
          <h3>PC Notes</h3>
          <p>The top row of notes (treble clef, ) are mapped from Q to U</p>
          <p>The middle row (alto clef, ) from A to J </p>
          <p>The bottom row (bass clef, ) from Z to M.</p>
        </div>
        <div className={style.notes}>
          <h3>Mobile Notes</h3>
          <p>
            This converter will mapped PC Notes to their corresponding key ("do"
            to "ti") with:
          </p>
          <p>
            Top row of notes (treble clef, ) as 3 or
            <span className={style.high}> yellow</span>
          </p>
          <p>
            Middle row (alto clef, ) as 2 or
            <span className={style.medium}> green</span>
          </p>
          <p>
            Bottom row (bass clef, ) as 1 or
            <span className={style.low}> red</span>
          </p>
        </div>
        <div className={style.notes}>
          <h3>PlayStation Notes</h3>
          <p>
            With a PlayStation controller, the notes from "do" to "ti" are
            mapped as
          </p>
          <p>
            <span className={style.ps_key}>do :  </span>
            <span className={style.ps_key}>re :  </span>
            <span className={style.ps_key}>mi : </span>
            <span className={style.ps_key}>fa :  </span>
            <span className={style.ps_key}>sol:  </span>
            <span className={style.ps_key}>la :  </span>
            <span className={style.ps_key}>ti :  </span>
          </p>
          <p className={style.lol}>
            It will look cryptic tho... what am i doing
          </p>
          <p>
            Top, middle, and bottom row of the notes are mapped by the converter
            in the same way as mobile:
          </p>
          <p>
            Top row of notes (treble clef, ) as 3 or
            <span className={style.high}> yellow</span>
          </p>
          <p>
            Middle row (alto clef, ) as 2 or
            <span className={style.medium}> green</span>
          </p>
          <p>
            Bottom row (bass clef, ) as 1 or
            <span className={style.low}> red</span>
          </p>
        </div>
        <div className={style.notes}>
          <h3>Color/Number Row Notes (Access via <i className='fas fa-cog'></i>icon on the Converter)</h3>
          <p>
            If number is turned off, color notes will be automatically turned
            on.
          </p>
        </div>
        <div className={style.notes}>
          <h4>Reference</h4>
          <a href="https://genshin-impact.fandom.com/wiki/Windsong_Lyre">
            Genshin Impact Fandom: Windsong Lyre
          </a>
        </div>
      </Box>
    </div>
  );
};

export default Help;
